
#app {
  margin: auto;
}
#app .tab {
    display: -ms-flexbox;
    display: flex;
    width: 100%;
    height: 40px;
    line-height: 40px;
    position: relative;
}
#app .tab:after {
      content: '';
      display: block;
      position: absolute;
      width: 100%;
      left: 0;
      bottom: 0;
      border-bottom: 1px solid rgba(7, 17, 27, 0.1);
}
#app .tab .tab-item {
      -ms-flex: 1;
          flex: 1;
      text-align: center;
}
#app .tab .tab-item a {
        display: block;
        font-size: 14px;
        color: #4d555d;
}
#app .tab .tab-item .router-link-active {
        color: #f01414;
}



